// В этом файле должны быть стили для БЭМ-блока main-nav, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.main-nav {

  $block-name:                &; // #{$block-name}__element

  position: relative;
  z-index: 10;

  &--header {

    .main-nav__list {
      display: flex;
      align-content: stretch;
      list-style: none;
      align-items: stretch;
      height: 42px;
      margin: 0;
      padding: 0;
    }

    .main-nav__item {
      // border: 1px solid #000;
      display: flex;
      width: 80px;
      height: 100%;
    }

    .main-nav__link {
      display: flex;
      align-content: center;
      justify-content: center;
      position: relative;
      width: 100%;
      height: 100%;
      font-style: 16px;
      font-family: $font-YanoneKaffeesatz;
      text-align: center;
      color: $col-menu-white;
      text-transform: uppercase;
      text-decoration: none;

      &:hover {
        background: linear-gradient(0deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.05));
        color: $col-menu-white;
        text-decoration: none;
      }

      p {
        width: 100%;
        margin: 0;
        padding: 0;
        margin-top: auto;
        margin-bottom: auto;
        text-align: center;
      }

      &.main-nav__link--active { /* stylelint-disable-line */
        background: $grad-blue;

        &:hover { /* stylelint-disable-line */
          background: $grad-blue;
        }
      }
    }
  }

  &--footer {

    .main-nav {

      &__list {
        display: flex;
        align-content: stretch;
        list-style: none;
        margin: {
          top: 12px;
          right: 0;
          bottom: 0;
          left: 0;
        };
        padding: 0;
      }

      &__item {
        position: relative;
        padding-left: 6px;
        padding-right: 6px;
        color: $text-dark-gray;
        font-size: 12px;
        font-family: $font-Droid-sans;

        &:after {
          content: '/';
          display: inline-block;
          position: absolute;
          top: 0;
          right: -3px;
        }
      }

      &__link {
        color: $text-dark-gray;
        text-decoration: none;

        &:hover {
          color: $logo-gray;
        }

        &.main-nav__link--active { /* stylelint-disable-line */
          color: $logo-blue;

          &:hover { /* stylelint-disable-line */
            color: $logo-gray;
          }
        }
      }
    }
  }
}
